import {
  JsonML,
  getTagName,
  getAttributes,
  getChildren,
  getLastChild,
} from '@ali/4ever-utils';
import { omit } from 'lodash-es';
import { MoState as State, MoAttributes as Attributes, createEmptyText } from '@ali/4ever-cangjie';
import { paragraphTags, normalizeJsonMLChildren as normalizeChildren, Phantom  } from '@ali/4ever-utils';

export const DATA_LINK_HREF_CANGJIE = 'data-link-href-cangjie';

function isLink(name: string, attrs: Attributes): boolean {
  return name === 'a' && ('href' in attrs || DATA_LINK_HREF_CANGJIE in attrs);
}

function normalizeLink(link: JsonML) {
  const tagName = getTagName(link);
  const attrs = getAttributes(link);
  const children = getChildren(link);

  const normalized: JsonML = attrs ? [tagName, attrs] : [tagName];
  children.forEach((child: JsonML): void => {
    if (paragraphTags[getTagName(child)]) {
      normalized.push(...getChildren(child));
    } else {
      normalized.push(child);
    }
  });
  return normalized;
}

function removeAttribute(attrs: Object | null, attr: string) {

  const result = omit(attrs, attr);

  // 如果结果已经没有其他 attribute，直接返回 null
  if (Object.keys(result).length === 0) {
    return null;
  }

  return result;
}

function dropColor(element: JsonML) {
  if (typeof element === 'string') {
    return element;
  }
  const tagName = getTagName(element);
  const attrs = getAttributes(element);
  const children = getChildren(element);

  const normalizeAttrs = removeAttribute(attrs, 'color');

  const normalized: JsonML = normalizeAttrs ? [tagName, normalizeAttrs] : [tagName];
  // 如果当前节点是叶子节点，则子节点一定是字符串，不需要额外处理 dropcolor
  if (attrs && attrs['data-type'] === 'leaf') {
    normalized.push(...children);
  } else {
    children.forEach((child: JsonML) => {
      if (paragraphTags[getTagName(child)]) {
        normalized.push(...(getChildren(child).map(dropColor)));
      } else {
        normalized.push(dropColor(child));
      }
    });
  }

  return normalized;
}

export default {
  name: 'a',

  onOpenTag(state: State, name: string, attrs: Attributes): boolean {
    if (!isLink(name, attrs)) return false;

    const link: JsonML = ['a', { href: attrs[DATA_LINK_HREF_CANGJIE] || attrs.href || '' }] as JsonML;
    state.push(link);
    return true;
  },

  onCloseTag(state: State, name: string, attrs: Attributes): boolean {
    if (!isLink(name, attrs)) return false;

    let link = state.pop();
    // HACK: 通过设置 data-title，覆盖 a.innerText 的值，用于实现钉钉文档 文档引用 功能
    if ('data-title' in attrs && attrs['data-title'].length) {
      link = [
        getTagName(link),
        getAttributes(link) as object,
        [
          'span',
          { 'data-type': 'text' },
          ['span', { 'data-type': 'leaf' }, attrs['data-title']],
        ],
      ];
    }

    const children = getChildren(link);
    if (children.length === 0) return true;

    link = dropColor(normalizeLink(normalizeChildren(link)));

    const parent = state.peek();
    // 如果是段落行，则直接 append
    if (paragraphTags[getTagName(parent)]) {
      state.append(link);
      return true;
    }

    const prevSibling = getLastChild(parent);
    if (prevSibling && (prevSibling as Phantom).isPhantom) {
      // 如果上一个兄弟节点是 phantom，则直接添加进去
      // 追加一个 text 节点 normalize，否则单测跑不过
      prevSibling.push(link, createEmptyText());
    } else {
      // 如果上一个兄弟节点不存在，或者不是 phantom 节点，则创建一个 phantom 节点
      const phantom: JsonML = ['p', createEmptyText(), link, createEmptyText()];
      (phantom as Phantom).isPhantom = true;
      state.append(phantom);
    }

    return true;
  },
};
